import React from 'react'
import classes from './index.module.scss';

export default () => {
	return (
		<div className={classes.titlebar}>
			{['Havayolu', 'Kalk', 'Var', 'Aktarma/Sre', 'Fiyat', 'Satn Al'].map(e => (
				<div>
					{e}
				</div>
			))}
		</div>
	)
}



